export default "<template>\r\n    <div class=\"MonHocToan\">\r\n        <raw-loader :mytxt=\"require('!raw-loader!./NDBaiViet.vue').default\"></raw-loader>\r\n\r\n        <h3 class=\"border-bottom\">Quan ly Bai Viet</h3>\r\n\r\n        <div class=\"\">\r\n            <div class=\"p-3\">\r\n                <b-row>\r\n                    <b-col :class=\"{ 'border': true, 'pt-3': true, 'pb-3': true, 'bg-light': (k % 2 == 0) ? true : false }\"\r\n                        lg=\"4\" v-for=\"(v, k) in items\" :key=\"k\">\r\n                        <h4 class=\"\">Id : {{ v.Id }} - {{ v.TieuDe }}</h4>\r\n                        <div class=\"text-right\">{{ v.Username }}</div>\r\n                        <div>\r\n                            {{ v.NoiDung.substring(0,120) }}\r\n                            <!-- <label>{{text.substring(0,5)}}</label> -->\r\n                        </div>\r\n                        <div class=\"text-right mt-5\">\r\n                            <button @click=\"onEdit(v)\" class=\"mr-1\">Edit</button>\r\n                            <button @click=\"onDel(v)\">Del</button>\r\n                        </div>\r\n                    </b-col>\r\n                </b-row>\r\n            </div>\r\n            <form @submit.stop.prevent=\"onSaveModal\">\r\n                <b-modal ref=\"my-modal\" size=\"md\" :no-close-on-backdrop=\"true\" hide-footer\r\n                    :title=\"(sel2[pri] == 0) ? 'form Add' : 'form Edit'\">\r\n                    <div class=\"d-block\">\r\n                        <div class=\"form-group row\" v-for=\"(v, k) in sel2\" :key=\"k\">\r\n                            <template v-if=\"(k == pri)\">\r\n                                <label class=\"col-sm-4 col-form-label\">Id</label>\r\n                                <div class=\"col-sm-8\">\r\n                                    <input :readonly=\"true\" :name=\"k\" v-model=\"sel2[k]\" :class=\"{\r\n                                        'form-control': true,\r\n                                        'ko-hop-le': errors.has(k),\r\n                                    }\" type=\"text\" />\r\n                                </div>\r\n                            </template>\r\n\r\n                            <template v-if=\"k == 'TieuDe'\">\r\n                                <label class=\"col-sm-4 col-form-label\">{{ k }}</label>\r\n                                <div class=\"col-sm-8\">\r\n                                    <input :name=\"k\" v-model=\"sel2[k]\" autocomplete=\"off\" :class=\"{\r\n                                        'form-control': true,\r\n                                        'ko-hop-le': errors.has(k),\r\n                                    }\" type=\"text\" v-validate=\"'required'\" />\r\n                                </div>\r\n                            </template>\r\n\r\n                            <template v-if=\"k == 'NoiDung'\">\r\n                                <label class=\"col-sm-4 col-form-label\">{{ k }}</label>\r\n                                <div class=\"col-sm-8\">\r\n                                    <!-- <input :name=\"k\" v-model=\"sel2[k]\" autocomplete=\"off\" :class=\"{\r\n                                        'form-control': true,\r\n                                        'ko-hop-le': errors.has(k),\r\n                                    }\" type=\"text\" v-validate=\"'required'\" /> -->\r\n\r\n                                    <b-form-textarea\r\n                                        id=\"textarea\"\r\n                                        v-model=\"sel2[k]\"\r\n                                        placeholder=\"Enter something...\"\r\n                                        rows=\"3\"\r\n                                        max-rows=\"6\"\r\n                                        ></b-form-textarea>\r\n\r\n\r\n                                </div>\r\n                            </template>\r\n\r\n                            <template v-if=\"k == 'UserId'\">\r\n                                <label class=\"col-sm-4 col-form-label\">{{ k }}</label>\r\n                                <div class=\"col-sm-8\">\r\n\r\n                                    <input :readonly=\"true\" :name=\"k\" v-model=\"sel2[k]\" :class=\"{\r\n                                        'form-control': true,\r\n                                        'ko-hop-le': errors.has(k),\r\n                                    }\" type=\"text\" />\r\n\r\n\r\n\r\n                                </div>\r\n                            </template>\r\n\r\n\r\n\r\n                        </div>\r\n                        <div class=\"text-right\">\r\n                            <button @click=\"onSaveModal\" class=\"btn mr-1\">Save</button>\r\n                            <button type=\"button\" class=\"btn\" @click=\"hideModal()\">Cancel</button>\r\n                        </div>\r\n                    </div>\r\n                </b-modal>\r\n            </form>\r\n\r\n            <b-row>\r\n                <b-col sm=\"6\" class=\"my-1\">\r\n                    <b-form-group label=\"Tìm kiếm\" label-for=\"filter-input\" label-cols-sm=\"3\" label-align-sm=\"right\"\r\n                        label-size=\"sm\" class=\"mb-0\">\r\n                        <b-input-group size=\"sm\">\r\n                            <b-form-input id=\"filter-input\" v-model=\"filter\" type=\"search\" placeholder=\"Type to Search\">\r\n                            </b-form-input>\r\n\r\n                            <b-input-group-append>\r\n                                <b-button :disabled=\"!filter\" @click=\"filter = ''\">Clear</b-button>\r\n                            </b-input-group-append>\r\n                        </b-input-group>\r\n                    </b-form-group>\r\n                </b-col>\r\n                <b-col sm=\"6\" class=\"my-1\">\r\n                    <b-form-group label=\"Số dòng\" label-for=\"per-page-select\" label-cols-sm=\"6\" label-cols-md=\"4\"\r\n                        label-cols-lg=\"3\" label-align-sm=\"right\" label-size=\"sm\" class=\"mb-0\">\r\n                        <b-form-select @input=\"onChangePerPage\" id=\"per-page-select\" v-model=\"perPage\"\r\n                            :options=\"pageOptions\" size=\"sm\">\r\n                        </b-form-select>\r\n                    </b-form-group>\r\n                </b-col>\r\n\r\n                <b-col sm=\"6\" class=\"my-1 text-right\">\r\n                    <span>page : {{ currentPage }} / {{ last_page }} of {{ totalRows }} </span>\r\n                    <b-button :disabled=\"adding == true || isEditing() == true\" class=\"mr-1 mb-1\" size=\"sm\"\r\n                        @click=\"OnAdd\">\r\n                        <b>Add</b></b-button>\r\n                </b-col>\r\n                <b-col sm=\"6\" class=\"my-1\">\r\n                    <b-pagination :disabled=\"adding == true || isEditing() == true\" ref=\"mypage\" v-model=\"currentPage\"\r\n                        :total-rows=\"totalRows\" :per-page=\"perPage\" size=\"sm\" align=\"fill\" @input=\"onChangePage\"\r\n                        first-text=\"First\" prev-text=\"Prev\" next-text=\"Next\" last-text=\"Last\">\r\n                    </b-pagination>\r\n                </b-col>\r\n            </b-row>\r\n            <!-- Info modal -->\r\n            <b-modal :id=\"infoModal.id\" :title=\"infoModal.title\" ok-only @hide=\"resetInfoModal\">\r\n                <pre>{{ infoModal.content }}</pre>\r\n            </b-modal>\r\n        </div>\r\n    </div>\r\n</template>\r\n<script>\r\n\r\nimport VeeValidate from \"vee-validate\";\r\nimport Vue from \"vue\";\r\nVue.use(VeeValidate, {\r\n    classes: true,\r\n    fieldsBagName: 'veeFields'\r\n});\r\n\r\nimport VueTypeaheadBootstrap from \"vue-typeahead-bootstrap\";\r\nimport { debounce } from \"lodash\";\r\n\r\nimport fetchClient from \"@/fetchClient\";\r\nimport TumLum from \"@/TumLum\";\r\n\r\nimport moment from \"moment\"\r\n\r\nexport default {\r\n    components: {\r\n        VueTypeaheadBootstrap,\r\n    },\r\n    data() {\r\n        return {\r\n            items: [\r\n            ],\r\n            fields: [],\r\n            totalRows: 1,\r\n            currentPage: 1,\r\n            perPage: 5,\r\n            pageOptions: [5, 10, 15, { value: 100, text: \"Show a lot\" }],\r\n            sortBy: '',\r\n            sortDesc: false,\r\n            sortDirection: 'asc',\r\n            filter: null,\r\n            filterOn: [],\r\n            infoModal: {\r\n                id: 'info-modal',\r\n                title: '',\r\n                content: ''\r\n            },\r\n            sel: {},\r\n            sel2: {},\r\n            pri: \"Id\",\r\n            last_page: 0,\r\n            adding: false,\r\n            ListAuto: [],\r\n\r\n            title: \"hello title\",\r\n            url: `/api/NDBaiViet`,\r\n        }\r\n    },\r\n\r\n    watch: {\r\n        last_page: function (newValue, oldValue) {\r\n        },\r\n        currentPage: function (val) {\r\n        },\r\n        totalRows: function (newValue, oldValue) {\r\n        }\r\n    },\r\n    computed: {\r\n    },\r\n    mounted() {\r\n        this.Lds(this.currentPage, this.perPage);\r\n    },\r\n    methods: {\r\n        onChangePerPage() {\r\n            this.Lds(this.currentPage, this.perPage);\r\n        },\r\n\r\n        OnSearch: debounce(function (query) {\r\n            fetch(`/api/Ban/AutoComplete?q=${query}`)\r\n                .then((response) => {\r\n                    return response.json();\r\n                })\r\n                .then((data) => {\r\n                    console.log(JSON.stringify(data))\r\n                    this.ListAuto = data.items;\r\n                });\r\n        }, 500),\r\n        onPagingInput() {\r\n        },\r\n        myProvider(ctx) {\r\n            let currentPage = ctx.currentPage;\r\n            let perPage = ctx.perPage;\r\n            let sortBy = ctx.sortBy;\r\n            let sortDesc = ctx.sortDesc;\r\n            let vm = this;\r\n            let url = `${this.url}/GetPage2`;\r\n            url = `${url}?page=${currentPage}&size=${perPage}&sortBy=${sortBy}&sortDesc=${sortDesc}`;\r\n            if (ctx.filter) {\r\n                let filter = ctx.filter;\r\n                url = `${url}&filter=${filter}`;\r\n            }\r\n            return fetchClient.get(url)\r\n                .then(res => {\r\n                    alert(JSON.stringify(res));\r\n                    vm.last_page = res.data.last_page;\r\n                    vm.totalRows = res.data.rowCount;\r\n                    vm.items = res.data.data;\r\n                    if (this.adding == true) {\r\n                        let r = {};\r\n                        r[this.pri] = 0;\r\n                        r[\"TenKhach\"] = \"xxx\";\r\n                        r[\"Ngay\"] = new Date();\r\n                        res.data.data.unshift(r);\r\n                    }\r\n                    return vm.mapData(res.data.data); //res.data.data;\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        mapData(data) {\r\n            let kq = [];\r\n            data.forEach(obj => {\r\n                let x = {};\r\n                Object.keys(obj).forEach(key => {\r\n                    let k = key;\r\n                    x[k] = obj[key];\r\n                });\r\n                kq.push(x);\r\n            })\r\n            return kq;\r\n        },\r\n        onChangePage() {\r\n            this.Lds(this.currentPage, this.perPage);\r\n            this.sel = {};\r\n            this.adding = false;\r\n        },\r\n        OnAdd() {\r\n            this.sel2 = {\r\n            }\r\n            this.sel2[this.pri] = 0;\r\n            this.sel2[\"TieuDe\"] = \"Ten bai viet\";\r\n            this.sel2[\"NoiDung\"] = \"Noi dung\";\r\n            this.showModal();\r\n        },\r\n        onEdit(item) {\r\n            //alert(`edit ${JSON.stringify(item)}`)\r\n            /*edit {\r\n                \"Id\":1,\r\n                \"TieuDe\":\"Lợi ích sự chia sẽ\",\r\n                \"NoiDung\":\"Làm rõ những ý tưởng của ban\",\r\n                \"UserId\":1,\r\n                \"Username\":\"admin\"}            \r\n            */\r\n            this.sel2 = JSON.parse(JSON.stringify(item));\r\n            this.showModal();\r\n        },\r\n        // OnEdit(item) {\r\n        //     alert('edit');\r\n        //     return;\r\n        //     if (JSON.stringify(this.sel) != \"{}\" || this.adding == true) {\r\n        //         return;\r\n        //     }\r\n        //     /*\r\n        //     this.sel2 = JSON.parse(JSON.stringify(item));\r\n        //     this.showModal();\r\n        //     */\r\n        //     this.getById(item[this.pri]);\r\n        //     // this.getById(item[this.pri]).then(res => {\r\n        //     //     this.sel2 = res.data;\r\n        //     //     this.showModal();\r\n        //     //{\"data\":{\"Id\":1,\"TenLop\":\"Lop 11A1sfsf\"},\"status\":200,\"statusText\":\"OK\",\"headers\":{\"content-length\":\"32\",\"            \r\n        //     // })\r\n        // },\r\n        onSaveModal() {\r\n            this.$validator.validateAll().then((result) => {\r\n                if (!result) {\r\n                    this.toast(\"Không hợp lệ\");\r\n                    return;\r\n                }\r\n                let r = JSON.parse(JSON.stringify(this.sel2));\r\n                if (r[this.pri] == 0) {\r\n                    this.Add(r);\r\n                } else {\r\n                    this.Update(r);\r\n                }\r\n            });\r\n        },\r\n\r\n        onSave(item) {\r\n            this.$validator.validateAll().then((result) => {\r\n                if (!result) {\r\n                    this.toast(\"Không hợp lệ\");\r\n                    return;\r\n                }\r\n                let r = JSON.parse(JSON.stringify(item));\r\n                if (r[this.pri] == 0) {\r\n                    this.Add(r).then(res => {\r\n                        if (res.data.kq == 'ok') {\r\n                            this.toast(`${res.data.msg}`);\r\n                            this.adding = false;\r\n                            this.$refs.mytable.refresh();\r\n                        }\r\n                    })\r\n                } else {\r\n                    //tien hanh sua\r\n                    this.Update(r).then(res => {\r\n                        this.toast(`${res.data.msg}`);\r\n                        this.adding = false;\r\n                        this.sel = {};\r\n                        this.$refs.mytable.refresh();\r\n                    })\r\n                }\r\n            });\r\n        },\r\n        OnCancel(item) {\r\n            if (item[this.pri] != 0) {\r\n                this.doCancel(item);\r\n                this.sel = {};\r\n            } else {\r\n                this.adding = false;\r\n                this.$refs.mytable.refresh();\r\n            }\r\n        },\r\n        onDel(item) {\r\n            //alert(`Del ${JSON.stringify(item)}`);\r\n            this.Del(item.Id);\r\n        },\r\n        /*\r\n        OnDel(item) {\r\n            this.Del(item[this.pri]).then(res => {\r\n                if (res.data.kq == 'nook') {\r\n                    this.toast(`${res.data.msg}`);\r\n                    return;\r\n                }\r\n                this.toast(`${res.data.msg}`);\r\n                let lastPage = this.getLastPage(this.totalRows - 1);\r\n                if (lastPage < this.currentPage) this.currentPage = lastPage;\r\n                this.$refs.mytable.refresh();\r\n            })\r\n        },\r\n        */\r\n        delRow(item) {\r\n            let id = item.id;\r\n            let index = this.items.findIndex(x => x.id == id);\r\n            this.items.splice(index, 1);\r\n        },\r\n        isEditing() {\r\n            return JSON.stringify(this.sel) != \"{}\"\r\n        },\r\n        isAdded() {\r\n            let kq = false;\r\n            this.items.forEach(x => {\r\n                if (x.id == 0) kq = true;\r\n            })\r\n            return kq;\r\n        },\r\n        doCancel(item) {\r\n            for (let key in item) {\r\n                if (item[key] != this.sel[key]) {\r\n                    item[key] = this.sel[key]\r\n                }\r\n            }\r\n        },\r\n        getLastPage(count) {\r\n            let pageSize = this.perPage;//this.tabulator.getPageSize();\r\n            let du = count % pageSize;\r\n            return du == 0 ? count / pageSize : (count - du) / pageSize + 1;\r\n        },\r\n        gotoPage(page) {\r\n            this.currentPage = page;\r\n            this.$refs.mytable.refresh();\r\n        },\r\n        toast(msg, variant = null) {\r\n            this.$bvToast.toast(msg, {\r\n                title: `Information`,\r\n                variant: variant,\r\n                solid: true,\r\n            });\r\n        },\r\n\r\n        async Add(r) {\r\n            return await fetchClient.post(`${this.url}/Add`, r)\r\n                .then(res => {\r\n                    //alert(JSON.stringify(res));\r\n                    //return;\r\n/*\r\n{\"data\":{\"msg\":\"Add BaiViet ok\"},\"status\r\n*/\r\n                    this.toast(`${res.data.msg}`);\r\n                    this.hideModal();\r\n                    //this.$refs.mytable.refresh();\r\n                    this.Lds(this.currentPage, this.perPage);\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async Update(r) {\r\n            return await fetchClient.post(`${this.url}/Update?Id=${r.Id}`, r)\r\n                .then(res => {\r\n                    //alert(JSON.stringify(res));\r\n\r\n                    this.hideModal();\r\n                    this.toast(`${res.data.msg}`)\r\n                    //this.$refs.mytable.refresh();\r\n                    this.Lds(this.currentPage, this.perPage);\r\n\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async Del(id) {\r\n            return await fetchClient.get(`${this.url}/Delete?id=${id}`)\r\n                .then(res => {\r\n                    this.toast(`${res.data.msg}`)\r\n                    this.Lds(this.currentPage, this.perPage);\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        async getById(id) {\r\n            return await fetchClient.get(`${this.url}/GetById?id=${id}`)\r\n                .then(res => {\r\n                    this.sel2 = res.data;\r\n                    this.showModal();\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n\r\n        async Lds(page, size) {\r\n            return await fetchClient.get(`/api/NDBaiViet/GetPage2?page=${page}&size=${size}`)\r\n                .then(res => {\r\n                    //alert(JSON.stringify(res));\r\n                    /*\r\n                    {\"data\":{\r\n                        \"last_page\":1,\r\n                        \"rowCount\":1,\r\n                        \"data\":[{\r\n                            \"Id\":1,\r\n                            \"TieuDe\":\"Lợi ích sự chia sẽ\",\r\n                            \"NoiDung\":\"Làm rõ những ý tưởng của ban\",\r\n                            \"UserId\":1}]},\"s\r\n                    */\r\n                    this.items = res.data.data;\r\n                    this.last_page = res.data.last_page;\r\n                    this.totalRows = res.data.rowCount;\r\n\r\n                    /*\r\n                    vm.last_page = res.data.last_page;\r\n                    vm.totalRows = res.data.rowCount;\r\n                    vm.items = res.data.data;\r\n                    if (this.adding == true) {\r\n                        let r = {};\r\n                        r[this.pri] = 0;\r\n                        r[\"TenKhach\"] = \"xxx\";\r\n                        r[\"Ngay\"] = new Date();\r\n                        res.data.data.unshift(r);\r\n                    }\r\n                    return vm.mapData(res.data.data); //res.data.data;\r\n                    */\r\n\r\n\r\n                    //alert(JSON.stringify(res));\r\n                    /*{\"data\":{\r\n                        \"last_page\":2,\r\n                        \"rowCount\":6,\r\n                        \"data\":[{\r\n                            \"Id\":1,\r\n                            \"TenLop\":\"Lớp 11A\"}\r\n                    */\r\n                })\r\n                .catch((err) => {\r\n                    TumLum.XuLyErr(err);\r\n                });\r\n        },\r\n        info(item, index, button) {\r\n            this.infoModal.title = `Row index: ${index}`\r\n            this.infoModal.content = JSON.stringify(item, null, 2)\r\n            this.$root.$emit('bv::show::modal', this.infoModal.id, button)\r\n        },\r\n        resetInfoModal() {\r\n            this.infoModal.title = ''\r\n            this.infoModal.content = ''\r\n        },\r\n        onFiltered(filteredItems) {\r\n            console.log('onFiltered' + JSON.stringify(filteredItems));\r\n            this.totalRows = filteredItems.length\r\n            this.currentPage = 1\r\n        },\r\n        showModal() {\r\n            this.$refs[\"my-modal\"].show();\r\n        },\r\n        hideModal() {\r\n            this.$refs[\"my-modal\"].hide();\r\n        },\r\n    }\r\n}\r\n</script>\r\n  \r\n  ";